export default {
  data () {
    return {
      goodsList: []
    }
  },

  onShow () {
    this.queryGoodsList()
    // 我们把vuex state.cart数组的长度计算放在vuex store
    wx.setTabBarBadge({
      index: 2, 
      text: this.$store.getters.getCartLen+''
    });
  },
  onHide () {
    let cart = this.goodsList.map(item => {
      return {
        goodsId: item.goodsId,
        num: item.num,
        checked: item.checked
      }
    })
    uni.setStorageSync('cart', cart)
  },
  computed: {
    isAll: {
      get () {
        // 所有商品都选中，返回true;也是说为true的个数和goodsLsit长度一致
        //   let len = 0
        //   this.goodsList.forEach(item => {
        //     if (item.checked) {
        //       len = len+1
        //     }
        //   })
        //   console.log(len)
        //   debugger
        //   if (len === this.goodsList.length) {
        //     return true
        //   } else {
        //     return false
        //   }
        // }

        //只要有一个不选中，就返回false
        // forEach的遍历不会被中断
        // this.goodsList.forEach(item => {
        //   console.log(item)
        //   if (!item.checked) {//没勾选
        //     return false
        //   }
        // })
        // return true

        // for of，可以中断
        // for(let item of this.goodsList){
        //   if(!item.checked){
        //     return false
        //   }
        // }
        // return true
        // 所有的商品都选中，就返回ture
        return this.goodsList.every(item => item.checked)
      },
      set (status) {
        // console.log(status)
        // 商品列表每一个元素的选中状态都应该和全选一致
        this.goodsList.forEach(item => {
          item.checked = status
        })
      }
    },
    //  总数量
    totalNum () {
      // 每一个被选中的商品数量之和
      // let sum =0
      // this.goodsList.forEach(item=>{
      //   if(item.checked){
      //     sum+=item.num
      //   }
      // })
      // return sum
      return this.goodsList.reduce((sum, item) => {
        return sum + (item.checked ? item.num : 0)
      }, 0)
    },
    // 总价
    totalPrice () {
      return this.goodsList.reduce((sum, item) => {
        return sum + (item.checked ? item.num * item.goods_price : 0)
      }, 0)
    }
  },
  watch:{
    goodsList:{
      handler(){
        // 把购物车商品列表状态的改变同步到state.cart
        this.$store.commit('updateCart',this.goodsList)
      },
      deep:true,
      // immediate:true //当goodsList刚声明时就触发handler
    }
  },
  methods: {
    toPay () {
      // 如果没有添加商品的话，就提示选择商品
      if (!this.totalNum) {
        uni.showToast({
          title: '请选择需要支付的商品',
          icon: 'none'
        })
        return
      }
      // 如果未登录，跳转登录
      let token = uni.getStorageSync('token')
      if (!token) {
        uni.navigateTo({ url: '/pages/login/login' });
        return
      }
      uni.navigateTo({ url: '/pages/pay/pay' });
    },
    // 全选的勾选/不勾选
    toggleAll () {
      this.isAll = !this.isAll
    },
    // 商品勾选/去勾选
    toggleCheck (item) {
      item.checked = !item.checked
    },
    // 商品数量++
    add (item) {
      item.num++
    },
    // 商品数量--
    sub (item) {
      item.num--
    },
    async queryGoodsList () {
      // 根据购物车数据（goodsId）去发请求
      let cart = this.$store.state.cart
      // 遍历cart数组，拿出里面goodsid拼接
      // let idsStr =''
      // cart.forEach(item=>{
      //   idsStr+=item.goodsId+','
      // })
      // idsStr.substr('-1',idsStr.length-1)
      // 如果购物车数据是空的，不必要发请求
      if (!cart.length) {
        return
      }

      let idsArr = cart.map(item => {
        return item.goodsId
      })
      let idsStr = idsArr.join(',')

      let _goodsList = await this.$request({
        url: `/api/public/v1/goods/goodslist?goods_ids=${idsStr}`
      })

      // goodsList和cart数据合并
      this.goodsList = cart.map(item => {
        // 从goodlist找到goods_id为item.goodsId的对象
        let goods = _goodsList.find(v => {
          return v.goods_id === item.goodsId
        })
        return { ...item, ...goods }
      })
    }
  }
}
